<template>
  <div class="container">
    <div class="card">
      <h2 class="subtitle" v-if="selectedInterview">{{ selectedInterview.title }}Ïùò ÏÉÅÏÑ∏ Í∏∞Ïà†ÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.</h2>
      <h2 v-else>Loading...</h2>
      <div class="box">
        <button class="rect" v-for="skill in skills" :key="skill.id" @click="selectSkillMethod(skill)">
          {{ skill.title }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../../plugins/axios';
import { mapActions, mapState } from 'vuex';

export default {
  computed: {
    ...mapState(['selectedInterview'])
  },
  data() {
    return {
      skills: []
    };
  },
  async created() {
    if (this.selectedInterview && this.selectedInterview.id) {
      try {
        const response = await axios.get(`/public/skills/${this.selectedInterview.id}`);
        this.skills = response.data; // ÏùëÎãµ Îç∞Ïù¥ÌÑ∞Î•º ÌååÏã±ÌïòÏó¨ skills Î∞∞Ïó¥Ïóê Ï†ÄÏû•
      } catch (error) {
        console.error('Error fetching skills:', error);
      }
    }
  },
  methods: {
    ...mapActions(['selectSkill']),
    selectSkillMethod(skill) {
      this.selectSkill(skill);
      this.$router.push({ name: 'InterviewStart' });
    }
  }
};
</script>
