  <template>
    <select v-model="selectedNumber" id="numberSelect" @change="emitSelectedNumber">
      <option v-for="number in numbers" :key="number" :value="number">{{ number }}</option>
     </select>
  </template>
  
  <script>
  export default {
    props: {
      maxNumber: {
        type: Number,
        required: true
      },
      modelValue: {
        type: Number,
        default: null
      }
    },
    data() {
      return {
        selectedNumber: this.modelValue // Î∂ÄÎ™®Î°úÎ∂ÄÌÑ∞ Ï¥àÍ∏∞ Í∞íÏùÑ Î∞õÏùå
      };
    },
    computed: {
      numbers() {
        // maxNumber Í∞íÏóê Îî∞Îùº 1Î∂ÄÌÑ∞ maxNumberÍπåÏßÄÏùò Ïà´Ïûê Î∞∞Ïó¥ ÏÉùÏÑ±
        return Array.from({ length: this.maxNumber }, (_, i) => i + 1);
      }
    },
    methods: {
      emitSelectedNumber() {
        // Î∂ÄÎ™® Ïª¥Ìè¨ÎÑåÌä∏ÏóêÍ≤å ÏÑ†ÌÉùÎêú Ïà´Ïûê Í∞íÏùÑ Ï†ÑÎã¨
        this.$emit('update:modelValue', this.selectedNumber);
      }
    },
    watch: {
      modelValue(newValue) {
        // Î∂ÄÎ™® Ïª¥Ìè¨ÎÑåÌä∏Î°úÎ∂ÄÌÑ∞ Í∞íÏù¥ Î≥ÄÍ≤ΩÎêòÎ©¥ Îç∞Ïù¥ÌÑ∞ ÏóÖÎç∞Ïù¥Ìä∏
        this.selectedNumber = newValue;
      }
    }
  };
  </script>  