                <!-- <th>Î¨∏Ìï≠Î≥Ñ ChatGPT ÎãµÎ≥Ä</th> -->
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in responseData.questionAnswerResponses" :key="index">
