<script>
import { mapMutations, mapActions, mapGetters } from 'vuex';

export default {
  data() {
    return {
      showingResult: false,
      responseData: {
        sessionId: null,
        interviewTitle: '',
        skillTitle: '',
        questionAnswerResponses: []
