<template>
  <div class="container">
    <div class="box">
      <h2 class="subtitle">Í∞úÎ∞úÏûê Î™®ÏùòÎ©¥Ï†ë ÏÇ¨Ïù¥Ìä∏Ïóê Ïò§Ïã† Í±∏ ÌôòÏòÅÌï©ÎãàÎã§.</h2>
      <p class="subtitle">Î©¥Ï†ë Ïú†ÌòïÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.</p>
      <div class="box" v-for="interview in interviews" :key="interview.id">
        <button class="button-text" @click="selectInterviewMethod(interview)">{{ interview.title }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../../plugins/axios';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      interviews: []
    };
  },
  async created() {
    try {
      const response = await axios.get('/public/interviews');
      this.interviews = response.data;// ÏùëÎãµ Îç∞Ïù¥ÌÑ∞Î•º ÌååÏã±ÌïòÏó¨ interviews Î∞∞Ïó¥Ïóê Ï†ÄÏû•
    } catch (error) {
      this.interviews = [];
      console.error('Error fetching interviews:', error);
    }
    if (this.interviews.length > 0) {
      this.selectInterviewMethod(this.interviews[0]);
    }
  },
  methods: {
    ...mapActions(['selectInterview']),
    selectInterviewMethod(interview) {
      this.selectInterview(interview);
      this.$router.push({ name: 'SkillSelection' });
    }
  }
};
</script>

