<template>
  <div class="container">
    <!-- ÏÉÅÎã® Î°úÎî©Î∞î -->
    <LoadingBar :isLoading="isLoading" />
    <div class="card">
      <!-- Ï§ëÍ∞ÑÏùò ÏßàÎ¨∏Í≥º ÎπÑÎîîÏò§ ÎÖπÌôî -->
      <div class="content">
        <div class="question-area">
          <p>{{ currentQuestionIndex }} / {{ interviewQuestions.length }}</p>
          <p v-if="currentQuestion">{{ currentQuestion.question }}
            <span @click="generateSpeech" class="speaker -on"></span>
          </p>
          <p v-else>Î°úÎî© Ï§ë...</p>
        </div>

        <div class="box" style="width: 800px">
          <p v-if="isRecording">REC {{ minutes }}Î∂Ñ {{ seconds }}Ï¥à</p>
          <div class="video-area" style="float: center;">
            <video controls autoplay playsinline ref="video" height="400" width="600"></video>
            <div>
              <button class="button-small" id="btn-start-recording" @click="stopAndstartRec">Îã§Ïãú ÎÖπÌôî</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- ÌïòÎã® Î≤ÑÌäº -->
    <div class="box">
      <button class="button-small" v-if="currentQuestion" @click="nextQuestion(currentQuestion)">Îã§Ïùå ÏßàÎ¨∏</button>
      <button class="button-small" style="width: 250px" v-if="currentQuestion" @click="endInterview(currentQuestion)">ÎãµÎ≥Ä Ï†ÄÏû• ÌõÑ Î©¥Ï†ë Ï¢ÖÎ£åÌïòÍ∏∞</button>
    </div>
  </div>
</template>

<script>
// import axios from '../../plugins/axios';
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex';
import RecordRTC from "recordrtc";
import LoadingBar from '../util/LoadingBar.vue';

export default {
  components: {
    LoadingBar
  },
  data() {
    return {
      isLoading: false,
      autoAnswer: '',
      recorder: null,
      synth: window.speechSynthesis,
      finalTranscripts: [], // ÏµúÏ¢Ö Í≤∞Í≥º
      isRecording: false, // ÏùåÏÑ± Ïù∏Ïãù ÏÉÅÌÉú ÌôïÏù∏
      recognition: null, // SpeechRecognition Í∞ùÏ≤¥ Ï†ÄÏû•
      elapsedTime: 180, // ÎÖπÌôî ÏãúÍ∞Ñ
      timer: null, // ÌÉÄÏù¥Î®∏ Î≥ÄÏàò,
      answerBlob: null,
    };
  },
  computed: {
    ...mapState(['interviewQuestions', 'currentQuestionIndex', 'answers']),
    ...mapGetters(['currentQuestion']),
    minutes() {
      return Math.floor(this.elapsedTime / 60); // Ï¥àÎ•º Î∂ÑÏúºÎ°ú Î≥ÄÌôò
    },
    seconds() {
      return this.elapsedTime % 60; // 60ÏúºÎ°ú ÎÇòÎàà ÎÇòÎ®∏ÏßÄÎ•º Ï¥àÎ°ú ÌëúÏãú
    }
  },
  watch: {
    currentQuestionIndex(newIndex) {
      this.autoAnswer = this.answers[newIndex] || '';
    }
  },
  mounted() {
    this.autoAnswer = this.answers[this.currentQuestionIndex] || '';
    this.startRec();
  },
  methods: {
    ...mapMutations(['addInterviewQA', 'decrementQuestionIndex']),
    ...mapActions(['updateInterviewIndex', 'loadInterviewQuestions', 'saveSession']),
    updateAnswer(question) {
      this.addInterviewQA(
        {"questionId": question.id, "question": question.question, "idealAnswer": question.idealAnswer,
        "answer": this.autoAnswer, "answerBlob": this.answerBlob});
    },
    // Deprecated
    previousQuestion() {
      if (this.currentQuestionIndex === 0) {
        alert('Ï≤´Î≤àÏß∏ ÏßàÎ¨∏ÏûÖÎãàÎã§.');
        return;
      }
      this.decrementQuestionIndex();
    },
    async nextQuestion(question) {
      if (this.currentQuestionIndex < this.interviewQuestions.length - 1) {
        await this.stopRec();
        this.updateAnswer(question);
        this.updateInterviewIndex();
        this.answer = '';
        this.startRec();
      } else {
        await this.endInterview(question);
      }
    },
    async endInterview(question) {
      this.isLoading = true;
      await this.stopRec();
      this.updateAnswer(question);
      //Î°úÍ∑∏Ïù∏ Í∏∞Îä•Ïù¥ Ï∂îÍ∞ÄÎêòÎ©¥ Í∑∏Îïå ÏÑúÎ≤ÑÏóê Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•ÌïòÍ∏∞ (ÌòÑÏû¨ Ï†ÄÏû•ÌïòÎäîÍ±¥ ÏùòÎØ∏ÏóÜÎã§. Îã§Ïãú Î≥º ÏàòÎèÑ ÏóÜÍ≥†)
      // await this.saveSession();
      this.$router.push({ name: 'CoupangAd' });
      this.isLoading = false;
    },
    generateSpeech() {
      const text = this.currentQuestion.question;
      this.textToSpeech(text);
    },
    textToSpeech(text) {
      const utterance = new SpeechSynthesisUtterance(text);
      this.synth.speak(utterance);
    },
    startToWriteDown() {
      this.recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
      this.recognition.lang = 'ko-KR';
      this.recognition.interimResults = true;

      this.recognition.onresult = (event) => {
        let final = '';

        for (const result of event.results) {
          if (result.isFinal) {
            final += result[0].transcript;
          }
        }
        if (final) {
          this.finalTranscripts.push(final);
        }
      };

      this.recognition.onerror = (event) => {
        console.error('Speech recognition error:', event.error);
      };

      this.recognition.onend = () => {
        if (this.isRecording) {
          this.recognition.start();
        }
      };

      this.isRecording = true;
      this.finalTranscripts = []; // ÏÉàÎ°úÏö¥ ÏÑ∏ÏÖò ÏãúÏûë Ïãú ÏµúÏ¢Ö Í≤∞Í≥º Î∞∞Ïó¥ Ï¥àÍ∏∞Ìôî
      this.recognition.start(); // ÏùåÏÑ± Ïù∏Ïãù ÏãúÏûë
    },
    stopWritingDown() {
      if (this.recognition) { 
        this.recognition.stop(); // ÏùåÏÑ± Ïù∏Ïãù Ï§ëÏßÄ
      }
      this.isRecording = false;
    },
    async stopAndstartRec() {
      await this.stopRec();
      this.startRec();
    },
    startRec() {
      this.finalTranscripts = [];
      this.$refs.video.srcObject = null;
      this.captureCamera(camera => {
        const video = this.$refs["video"];
        video.muted = true;
        video.volume = 0;
        video.srcObject = camera;
        this.recorder = RecordRTC(camera, {
          type: "video",
          mimeType: 'video/webm', // Ïõπ Î∏åÎùºÏö∞Ï†ÄÏóêÏÑú Í∞ÄÏû• Ìò∏ÌôòÏÑ± Ï¢ãÏùÄ Ìè¨Îß∑
          videoBitsPerSecond: 2500000, // ÎπÑÌä∏Î†àÏù¥Ìä∏ ÏÑ§Ï†ï (2.5Mbps)
          frameRate: 30 // ÌîÑÎ†àÏûÑ ÏÜçÎèÑ ÏÑ§Ï†ï
        });
        this.recorder.startRecording();
        this.recorder.camera = camera;

        this.startTimer(); // ÌÉÄÏù¥Î®∏ ÏãúÏûë
      });
      // this.startToWriteDown();
      this.isRecording = true; // ÎÖπÌôî Ï§ë ÏÉÅÌÉúÎ°ú Î≥ÄÍ≤Ω
    },
    captureCamera(callback) {
      navigator.mediaDevices
      .getUserMedia({
        video: {
          width: { ideal: 1280 }, // ÏõêÌïòÎäî Ìï¥ÏÉÅÎèÑ (Í∞ÄÎ°ú)
          height: { ideal: 720 }, // ÏõêÌïòÎäî Ìï¥ÏÉÅÎèÑ (ÏÑ∏Î°ú)
          frameRate: { ideal: 30, max: 60 } // ÌîÑÎ†àÏûÑ ÏÜçÎèÑ
        },
        audio: true
      })
        .then(function(camera) {
          callback(camera);
        })
        .catch(function(error) {
          alert("Unable to capture your camera. Please check console logs.");
          console.error(error);
        });
    },
    stopRec() {
      return new Promise((resolve) => {
        this.stopTimer(); // ÌÉÄÏù¥Î®∏ Ï§ëÏßÄ
        
        try {
          // ÎÖπÌôî Ï§ëÏù∏ÏßÄ ÌôïÏù∏ÌïòÍ≥†, recorderÍ∞Ä ÏûàÎäî Í≤ΩÏö∞ÏóêÎßå stopRecording Ìò∏Ï∂ú
          this.recorder.stopRecording(this.stopRecordingCallback);
          this.isRecording = false; // ÎÖπÌôî Ï§ë ÏÉÅÌÉú Ìï¥Ï†ú
          
          this.recorder.stopRecording(() => {
            this.stopRecordingCallback();
            resolve(); // ÎÖπÌôîÍ∞Ä ÎÅùÎÇú ÌõÑÏóê resolve Ìò∏Ï∂ú
          });
          // this.stopWritingDown();
          this.autoAnswer = this.finalTranscripts.join(' ');
          this.isRecording = false; // ÎÖπÌôî Ï§ë ÏÉÅÌÉú Ìï¥Ï†ú
        } catch (e) {
          alert('ÎπÑÎîîÏò§Í∞Ä Î°úÎî©Ï§ëÏûÖÎãàÎã§. Ïû†ÏãúÎßå Í∏∞Îã§Î†§Ï£ºÏÑ∏Ïöî.');
        }
        
      });
    },
    stopRecordingCallback() {
      const video = this.$refs["video"];
      if (video) {
        video.srcObject = null;
        video.muted = false;
        video.volume = 1;
      }
      
      this.answerBlob = this.recorder.getBlob();
      this.recorder.camera.stop();
      this.recorder.destroy();
      this.recorder = null;
    },
    startTimer() {
      this.elapsedTime = 180; // 3Î∂Ñ (180Ï¥à)ÏóêÏÑú ÏãúÏûë
      this.timer = setInterval(() => {
        this.elapsedTime--; // 1Ï¥àÏî© Í∞êÏÜå
        if (this.elapsedTime <= 0) {
          clearInterval(this.timer); // ÌÉÄÏù¥Î®∏ Ï¢ÖÎ£å
          this.nextQuestion(this.currentQuestion); // 0Ï¥àÍ∞Ä ÎêòÎ©¥ ÏûêÎèôÏúºÎ°ú Îã§Ïùå ÏßàÎ¨∏ÏúºÎ°ú ÎÑòÏñ¥Í∞ê
        }
      }, 1000); // 1Ï¥àÎßàÎã§ Ïã§Ìñâ
    },
    stopTimer() {
      clearInterval(this.timer);
    }
  },
};
</script>
